<template lang="pug">
p-slide(v-if="about")
  template(#header)
    b-row
      b-col(sm="4").p-sm-0
        b-row(align-v="center").photoAbout.mx-auto.h-100.text-center.p-3.p-sm-1
          img(:src="`${$root.publicPath}images/myimg.jpg`" 
               alt="avatar").img-fluid.img-thumbnail
      b-col
        b-row(align-v="center").h-100
          b-col
            h1 {{about.name}}
            transition(name="_prof" mode="out-in")
              p(:key="prof") {{prof | capitalize}}
            ul.list-inline
              li.list-inline-item.mx-2.soc(v-for="s in about.social").bg-white
                b-link(:href="s.link" target="_blank" :class="'fab fa-'+s.name+' fa-lg'").theme-color
  b-row.mt-sm-2.mt-md-4
    b-col(md)
      h2 {{about.desc.title[0]}} 
        strong.theme-color {{about.desc.title[1]}}
      p {{about.desc.text}}
    b-col(md)
      b-row.h-100
        b-col
          ul.list-unstyled.about-list.d-flex.flex-column.justify-content-around
            li(v-for="el in about.infoList")
              span.title.text-dark {{el.key}}
              span.value.text-secondary {{el.value}}
</template>
<script>
import PSlide from '@/components/Slide.vue';
import SlideFetch from '@/components/SlideFetchMixin';

export default {
    data(){
      return{
        about: null,
        prof: ""
      }
    },
    async beforeCreate(){
      await SlideFetch.fetchDataJson('about')
      .then(res => {
        this.about = res;
      });
    },
    mountedt(){
      this.profCarousel();
    },
    methods:{
      profCarousel(){
        let i = 0;
        this.prof = about.prof[i++];
        setInterval(()=>{
          this.prof = about.prof[i++];
          if(i >= about.prof.length) i = 0;
        }, 5000);
      }
    },
    components:{
      PSlide
    }
}
</script>
<style scoped lang="scss">
.about-list{
  li {
    margin: 5px 0;
    span{
      display: inline-block;
    }
  }
  .title{
    font-weight: 500;
    min-width: 100px;
  }
}
.photoAbout{
  max-width: 250px;
}
._prof-enter-active,._prof-leave-active{
  
  transition: transform 0.3s ease-in;
}
._prof-enter,._prof-leave-to{
  transform: rotateY(90deg);
}
._prof-enter-to,._prof-leave{
  transform: rotateY(0deg);
}
</style>
<style lang="scss">
.soc{
  width: 35px;
  height: 35px;
  text-align: center;
  text-decoration: none;
  border-radius: 50%;
  cursor: pointer;
  transition-duration: 0.2s;
  a{
    line-height: 35px;
    white-space: nowrap;
    text-decoration: none;
    &:hover {
      text-decoration: none;
      
    }
  }
  &:hover {
    box-shadow: 0 0 8px #dddddd;
  }
}

</style>