import Vue from 'vue'
import VueRouter from 'vue-router'
import AboutSlide from '../views/AboutSlide.vue';
Vue.use(VueRouter)

const routes = [
  {
    path: '/about',
    alias: '/',
    name: 'About',
    component: AboutSlide
  },
  {
    path: '/resume',
    name: 'Resume',
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/ResumeSlide.vue')
    }
  },
  {
    path: '/portfolio',
    name: 'Portfolio',
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/PortfolioSlide.vue')
    }
  },
  {
    path: '*',
    redirect: '/'
  }
]

const router = new VueRouter({
  mode: 'history',
  routes,
  scrollBehavior (to, from, savedPosition) {
    if(savedPosition){
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
});

export default router
