
var cache = {}
export default {
  async fetchDataJson(filename){
    if(cache[filename]){
      return cache[filename];
    }
    return fetch(`data/${filename}.json`)
    .then(res => {
      if(res.ok){
        return res.json();
      }else{
        throw new Error('Resourse not loaded');
      }
    })
    .then(json => {
      return (cache[filename] = json);
    })
    .catch(err => {
      return err instanceof Error ? err.message : err;
    });
  }
}